
.player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../../assets/Background.png) no-repeat;
  background-size: cover;
}
.player .player-wrapper {
    width: 100%;
    height: 100%;
}
.player .player-wrapper .player-top {
      width: 90%;
      margin: 0 auto;
}
.player .player-wrapper .player-top .song .song-pic {
        width: 162px;
        height: 162px;
        margin: 0 auto;
        line-height: 162px;
        border-radius: 50%;
        overflow: hidden;
        box-shadow: 0 0 55px black;
}
.player .player-wrapper .player-top .song .song-pic .loading-enter-active, .player .player-wrapper .player-top .song .song-pic .loading-leave-active {
          transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.player .player-wrapper .player-top .song .song-pic .loading-enter, .player .player-wrapper .player-top .song .song-pic .loading-leave-active {
          opacity: 0;
}
.player .player-wrapper .player-top .song .song-pic .loading {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          width: 100%;
          height: 100%;
          font-size: 50px;
          color: #fff;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          text-align: center;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon {
            width: 100px;
            height: 100px;
            position: relative;
            margin: 0 auto;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span {
              display: inline-block;
              width: 16px;
              height: 16px;
              border-radius: 50%;
              background: lightgreen;
              position: absolute;
              -webkit-animation: load 1.04s ease infinite;
                      animation: load 1.04s ease infinite;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(1) {
                left: 0;
                top: 50%;
                margin-top: -8px;
                -webkit-animation-delay: 0.13s;
                        animation-delay: 0.13s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(2) {
                left: 14px;
                top: 14px;
                -webkit-animation-delay: 0.26s;
                        animation-delay: 0.26s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(3) {
                left: 50%;
                top: 0;
                margin-left: -8px;
                -webkit-animation-delay: 0.39s;
                        animation-delay: 0.39s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(4) {
                top: 14px;
                right: 14px;
                -webkit-animation-delay: 0.52s;
                        animation-delay: 0.52s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(5) {
                right: 0;
                top: 50%;
                margin-top: -8px;
                -webkit-animation-delay: 0.65s;
                        animation-delay: 0.65s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(6) {
                right: 14px;
                bottom: 14px;
                -webkit-animation-delay: 0.78s;
                        animation-delay: 0.78s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(7) {
                bottom: 0;
                left: 50%;
                margin-left: -8px;
                -webkit-animation-delay: 0.91s;
                        animation-delay: 0.91s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(8) {
                bottom: 14px;
                left: 14px;
                -webkit-animation-delay: 1.04s;
                        animation-delay: 1.04s;
}
.player .player-wrapper .player-top .song .song-pic .song-pic {
          width: 100%;
          height: 100%;
}
.player .player-wrapper .player-top .song .song-pic .song-pic-enter-active, .player .player-wrapper .player-top .song .song-pic .song-pic-leave-active {
          transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.player .player-wrapper .player-top .song .song-pic .song-pic-enter, .player .player-wrapper .player-top .song .song-pic .song-pic-leave-active {
          -webkit-transform: scale(0);
                  transform: scale(0);
}
.player .player-wrapper .player-top .song .song-info {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        padding: 50px 0;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        text-align: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        font-size: 22px;
}
.player .player-wrapper .player-top .song .song-info .stats {
          -webkit-box-flex: 0;
              -ms-flex: 0 0 40px;
                  flex: 0 0 40px;
          color: #1b3f70;
}
.player .player-wrapper .player-top .song .song-info .song-and-band {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          padding: 0 16px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
}
.player .player-wrapper .player-top .song .song-info .song-and-band .song-name {
            padding-bottom: 10px;
            font-size: 18px;
            color: #fff;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
}
.player .player-wrapper .player-top .song .song-info .song-and-band .band {
            font-size: 16px;
            font-weight: bold;
            color: #183965;
            text-transform: uppercase;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
}
.player .player-wrapper .player-top .song .song-info .like {
          -webkit-box-flex: 0;
              -ms-flex: 0 0 40px;
                  flex: 0 0 40px;
          color: #ff3334;
}
.player .player-wrapper .playback-progress {
      width: 100%;
}
.player .player-wrapper .playback-progress .time {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        width: 90%;
        margin: 0 auto;
        margin-bottom: 10px;
        color: #fff;
        font-size: 16px;
}
.player .player-wrapper .playback-progress .time .now {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          text-align: left;
}
.player .player-wrapper .playback-progress .time .total {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          text-align: right;
}
.player .player-wrapper .playback-progress .player-bar {
        position: relative;
        width: 100%;
        height: 5px;
        background: #031a3a;
}
.player .player-wrapper .playback-progress .player-bar .player-now {
          position: absolute;
          top: 0;
          left: 0;
          height: 5px;
          background: #ff3334;
          transition: all 0.5s;
}
.player .player-wrapper .player-bottom {
      width: 90%;
      margin: 0 auto;
}
.player .player-wrapper .player-bottom .play {
        width: 100%;
        padding: 50px 0;
}
.player .player-wrapper .player-bottom .play .play-function {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          width: 98%;
          padding: 30px 0;
          margin: 0 auto;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
}
.player .player-wrapper .player-bottom .play .play-function .function {
            -webkit-box-flex: 1;
                -ms-flex: 1;
                    flex: 1;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            padding: 0 5px;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
}
.player .player-wrapper .player-bottom .play .play-function .function .item {
              -webkit-box-flex: 1;
                  -ms-flex: 1;
                      flex: 1;
              text-align: center;
              color: #0d3f84;
}
.player .player-wrapper .player-bottom .play .play-function .function .player-btn {
              -webkit-box-flex: 2;
                  -ms-flex: 2;
                      flex: 2;
              color: #ff3334;
}
.player .player-wrapper .player-bottom .play .play-function .function .player-btn .icon {
                font-size: 70px;
}
.player .player-wrapper .player-bottom .play .play-function .icon {
            font-size: 40px;
}
.player .player-wrapper .player-bottom .play .play-function .cycle-mode .icon,
          .player .player-wrapper .player-bottom .play .play-function .repeat .icon {
            font-size: 22px;
}
.player .player-wrapper .player-bottom .check {
        font-size: 18px;
        letter-spacing: 3px;
        text-transform: uppercase;
        text-align: center;
        color: #162f53;
        padding: 10px 0;
}
@-webkit-keyframes loading {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes load {
0% {
    opacity: 1;
}
100% {
    opacity: 0.2;
}
}
@keyframes load {
0% {
    opacity: 1;
}
100% {
    opacity: 0.2;
}
}



