
.navigation .fade-enter-active, .navigation .fade-leave-active {
  transition: all 0.5s;
}
.navigation .fade-enter, .navigation .fade-leave-active {
  opacity: 0;
}
.navigation .overlay {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}
.navigation .btn {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 150px;
  height: 150px;
  text-align: center;
  color: #fff;
  border-top-left-radius: 150px;
  overflow: hidden;
}
.navigation .btn.canshow {
    width: 70px;
    height: 70px;
    border-top-left-radius: 150px;
    overflow: hidden;
    transition: all 0.8s;
}
.navigation .btn .wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-top-left-radius: 150px;
}
.navigation .btn .pop-enter-active, .navigation .btn .pop-leave-active {
    transition: all 0.8s;
}
.navigation .btn .pop-enter, .navigation .btn .pop-leave-active {
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
}
.navigation .btn .icon {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 70px;
    height: 70px;
    line-height: 70px;
    font-size: 22px;
    text-align: center;
    background: #fff;
    border-top-left-radius: 70px;
    z-index: 100;
    color: #f06060;
    transition: all 0.3s;
}
.navigation .btn .icon.canshow {
      background: rgba(255, 255, 255, 0.3) !important;
      transition: all 5s;
}
.navigation .btn .icon i {
      position: absolute;
      top: 50%;
      left: 50%;
}
.navigation .btn ul {
    width: 100%;
    height: 100%;
}
.navigation .btn ul li {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 150px;
      height: 150px;
      -webkit-transform-origin: 100% 100%;
              transform-origin: 100% 100%;
      overflow: hidden;
      background: #901911;
}
.navigation .btn ul li:nth-child(1) {
        background-color: #f44e4e;
        -webkit-transform: rotate(0) skew(60deg);
                transform: rotate(0) skew(60deg);
}
.navigation .btn ul li:nth-child(2) {
        background-color: #f45757;
        -webkit-transform: rotate(30deg) skew(60deg);
                transform: rotate(30deg) skew(60deg);
}
.navigation .btn ul li:nth-child(3) {
        background-color: #f44e4e;
        -webkit-transform: rotate(60deg) skew(60deg);
                transform: rotate(60deg) skew(60deg);
}
.navigation .btn ul li a {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 300px;
        height: 300px;
        padding-top: 18px;
        font-size: 20px;
        -webkit-transform: skew(-60deg) rotate(-73deg) scale(1);
                transform: skew(-60deg) rotate(-73deg) scale(1);
        text-align: center;
        color: #fff;
}



